.App {
    background-color: rgb(36, 10, 59);
    min-height: 500px;
    width: 400px;
    margin: 40px auto;
    box-shadow: 2px 3px 5px 3px green;
}

#to-do-form input {
    background-color: rgb(32, 61, 223);
    border: 0;
    width: 200px;
    height: 50px;
    padding: 0 20px;
    margin: 20px;
    border-radius: 5px;
    color: #ffffff;
    /* font-weight: bolder; */
    font-size: 18px;
}

#to-do-form input::placeholder {
    color: white;
}

#to-do-form input:focus,
#to-do-form button:focus {
    outline: none;
}

#to-do-form button {
    height: 50px;
    width: 80px;
    border-radius: 5px;
    border: 0;
    background-color: rgb(218, 221, 53);
    color: #000;
    font-weight: bold;
    cursor: pointer;
}