.list {
    position: relative;
    color: #fff;
    width: 300px;
    background-color: rgb(138, 29, 111);
    height: 40px;
    margin: 5px auto;
    border-radius: 5px;
}

.list p {
    padding: 10px;
    font-size: 20px;
}

.list p span {
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.faicon {
    margin: 0 5px;
    font-size: 15px;
}

.list p input {
    background-color: transparent;
    border: 0;
    color: #fff;
    font-size: 18px;
}

.list p input:focus {
    outline: none;
}